import React, { useState, useEffect } from "react";
import Masonry from "react-masonry-css";
import axios from "axios";
import { message } from "antd";
import { useSelector, useDispatch } from "react-redux";
import { dataRefresh } from "../service/redux/actions";
import ImageCard from "../components/ImageCard";
import EndPic from "../images/cyberchosisBocchi.gif";
const HomePage = () => {
  const dispatch = useDispatch();
  // 
  const breakpointColumnsObj = {
    default: 4, // 4
    1400: 3,
    992: 2, // 1100px2
    768: 1 // 700px1
  };
  // data
  const [dataList, setDataList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1); // 
  const [isEndPage, setIsEndPage] = useState(false);

  const getData = async (page) => {
    try {
      setLoading(true);
      const res = await axios.get(
        `${process.env.REACT_APP_API_URL}/api/card?page=${page}&limit=20`
      );

      if (res.data.success && page === 1) {
        setDataList(res.data.cardData);
        setLoading(false);
      } else if (res.data.success && page > 1) {
        setDataList((prevDataList) => [...prevDataList, ...res.data.cardData]);
        setLoading(false);
      }
    } catch (err) {
      console.error(err);
      setLoading(false);
      setIsEndPage(true);
      if (err.response && err.response.status === 404) {
        message.error(`Error: ${err.message}`, 5);
      } else {
        message.warning(
          `Error: ${err.response.data.message || err.response.statusText}`,
          5
        );
      }
    }
  };
  useEffect(() => {
    if (currentPage === 1) {
      getData(1);
    } else {
      getData(currentPage);
    }
  }, [currentPage]);

  // 
  const handleScroll = () => {
    const isBottom =
      window.innerHeight + document.documentElement.scrollTop ===
      document.documentElement.offsetHeight;
    if (isBottom && !loading) {
      setCurrentPage((prevPage) => prevPage + 1); //  1
    }
  };
  useEffect(() => {
    if (!isEndPage) {
      window.addEventListener("scroll", handleScroll);
    }
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [loading]);
  //  loading 

  // redux 
  const refreshPage = async (needsRefresh) => {
    if (needsRefresh) {
      if (currentPage === 1) {
        await getData(1);
      } else {
        setCurrentPage(1);
      }
      setIsEndPage(false);

      window.scrollTo(0, 0);

      dispatch(dataRefresh(false));
    }
  };
  const reduxDataOperation = useSelector((state) => state.data);
  useEffect(() => {
    refreshPage(reduxDataOperation.needsRefresh);
  }, [reduxDataOperation]);

  return (
    <div id="home_page">
      <div className="card_container container">
        <Masonry
          breakpointCols={breakpointColumnsObj}
          className="cards_masonry_grid"
          columnClassName="cards_masonry_grid_column"
        >
          {/*  */}
          {dataList.length > 0 &&
            dataList.map((data, index) => (
              <ImageCard
                cardData={data}
                key={data.card_id}
                headerOptions={false}
              />
            ))}
        </Masonry>
        {loading && (
          <div className="fw-2 fw-bold text-center">Loading . . .</div>
        )}
        {!loading && isEndPage && (
          <div className="end_card">
            <h1>This is END</h1>
            <div className="img_box">
              <img src={EndPic} alt="" />
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default HomePage;
